@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;1,100;1,200&family=Work+Sans:wght@300&display=swap');

.App{
    min-height:100vh;
    display:flex;
    background-image: url("./background.jpg");
    background-size: cover;
    background-position: center;
    
}

::-webkit-scrollbar {
    width: 0px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: rgba(136, 136, 136, 0.281);
  }
  
  /* Handle on hover */
   ::-webkit-scrollbar-thumb:hover {
    background: #555;
  } 